import { PropType, ExtractPropTypes, VNode } from "vue";
import { TableProps } from "ant-design-vue";
import { ColumnType } from "ant-design-vue/lib/table/interface";

//  column
export enum AmTableColumnEnum {
  TEXT = "text", // 
  LINK = "link", // 
  IMAGE = "img", // 
  DATE = "date", // 
  TIME = "time", // 
  POPOVER = "popover", // 
}

export type AmTableColumnType =
  | "text"
  | "link"
  | "img"
  | "date"
  | "time"
  | "popover";

type enumColumnType<T> = { [key: string]: T };

//  column
export interface AmTableColumn extends ColumnType {
  type?: string; // 
  format?: string; //  
  imgWidth?: number; // 
  maxlength?: number; // 
  link?: boolean; // 
  enumConfig?: enumColumnType<string | number>; // 
  render?: (params: any) => VNode;
}

// 
export interface AmTableData {
  columns: Array<AmTableColumn>;
  loading?: boolean;
  page?: number;
  size?: number;
  [key: string]: any;
}

type RowSelectionType = "checkbox" | "radio";

// 
export interface TableRowSelection {
  type: RowSelectionType;
  onChange?: (selectedRowKeys: any[], selectedRows: any[]) => void | undefined;
}

//  
export const AmTableProps = {
  data: {
    // 
    type: Object,
    default: () => { },
  },
  totalColumns: {
    // 
    type: String,
    default: "total",
  },
  mainElement: {
    type: String,
    default: ".main-content",
  },
  dataColumn: {
    // 
    type: String,
    default: "list",
  },
  isShowPagination: {
    // 
    type: Boolean,
    default: true,
  },
  //  
  paginationType: {
    type: String as PropType<"default" | "small" | undefined>,
    default: "",
  },
  //  
  currentColumn: {
    type: String,
    default: "page",
  },
  //  
  pageSizeColumn: {
    type: String,
    default: "size",
  },
  page: {
    type: Number,
    default: 1,
  },
  size: {
    type: Number,
    default: 10,
  },
  // key
  rowKey: {
    type: String,
    default: "key",
  },
  //  
  pageSizeOptions: {
    type: Array as PropType<Array<number | string>>,
    default: () => ["10", "20", "30", "50", "100"],
  },
  bordered: {
    type: Boolean,
    default: true,
  },
  rowSelection: {
    type: Object as PropType<Record<string, any>>,
    default: null,
  },
  defaultShowText: {
    type: String,
    default: "-",
  },
};

// table
export type AmTableProps = ExtractPropTypes<typeof AmTableProps> & TableProps;
