import { ExtractPropTypes, PropType } from 'vue';

//  
export type SearchItemType = 'select' | 'input' | 'RangePicker' | 'cascader';

//  
export interface OptionItem {
  label: string;
  value: string | number;
  children?: Array<OptionItem>;
}

export interface SearchItemConfig {
  fieldNames?: any;
  format?: string | undefined;
  name?: string; //  name
  label: string; //  label
  type?: SearchItemType; //  
  width?: number | string; //  
  placeholder?: string | any[]; //  placeholder
  defaultValue?: number | string | any[]; //  
  allowClear?: boolean; //  
  options?: Array<OptionItem>; //  
  key: string; //  name
  disabled?: boolean;
  onChange?: (...args: any[]) => void;
}

export const AmSearchProps = {
  //  
  dataSource: {
    type: Array<unknown>,
    default: () => []
  },
  value: {
    //  
    type: Object as PropType<Record<string, any>>
  },
  showLen: {
    type: Number,
    default: 0 //  0
  },
  //  
  showSearch: {
    type: Boolean,
    default: true
  },
  //  /
  showToggle: {
    type: Boolean,
    default: false
  },
  searchText: {
    type: String,
    default: ''
  },
  resetText: {
    type: String,
    default: ''
  },
  //  gutter
  gutter: {
    type: Number,
    default: 24
  },
  //  
  span: {
    type: Number,
    default: 8
  },
  // <576px 
  xs: {
    type: Number,
    default: 24
  },
  //  576px 
  sm: {
    type: Number,
    default: 12
  },
  // 768px 
  md: {
    type: Number,
    default: 8
  },
  // 992px 
  lg: {
    type: Number,
    default: 6
  },
  // 1200px 
  xl: {
    type: Number,
    default: 4
  },
  //  1600px 
  xxl: {
    type: Number,
    default: 4
  }
};

export type AmSearchProps = ExtractPropTypes<typeof AmSearchProps>;
